@import url(https://fonts.googleapis.com/css?family=Poiret+One);

@import url(https://cdnjs.cloudflare.com/ajax/libs/weather-icons/2.0.9/css/weather-icons.min.css);

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root {
    --rad: 0.7rem;
    --dur: 0.3s;
    --color-dark: #2f2f2f;
    --color-light: #fff;
    --color-brand: #57bd84;
    --font-fam: "Lato", sans-serif;
    --height: 5rem;
    --btn-width: 6rem;
    --bez: cubic-bezier(0, 0, 0.43, 1.49);
}

body {
    background-color: #f5f5f5;
    background: var(--color-dark);
    /* height: 100vh; */
    min-height: 100vh;
    display: grid;
    place-items: center;
}

.widget {
    display: flex;
    height: 400px;
    width: 650px;
    flex-wrap: wrap;
    cursor: pointer;
    border-radius: 20px;
    box-shadow: 0 27px 55px 0 rgba(0, 0, 0, 0.3),
        0 17px 17px 0 rgba(0, 0, 0, 0.15);
}

.widget .weatherIcon {
    flex: 1 100%;
    height: 50%;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    background: #fafafa;
    font-family: weathericons;
    display: flex;
    align-items: center;
    justify-content: space-around;
    font-size: 100px;
}

.widget .weatherInfo {
    flex: 0 0 70%;
    height: 40%;
    background: #080705;
    display: flex;
    align-items: center;
    color: white;
}

.widget .weatherInfo .temperature {
    flex: 0 0 40%;
    width: 100%;
    font-size: 65px;
    display: flex;
    justify-content: space-around;
    margin-right: 20px;
}

.widget .weatherInfo .description {
    flex: 0 60%;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    justify-content: center;
}

.widget .weatherInfo .description .weatherCondition {
    text-transform: uppercase;
    font-size: 35px;
    font-weight: 100;
}

.widget .weatherInfo .description .place {
    font-size: 15px;
}

.widget .date {
    flex: 0 0 30%;
    height: 40%;
    background: #70c1b3;
    display: flex;
    justify-content: space-around;
    align-items: center;
    color: white;
    font-size: 30px;
    font-weight: 800;
    text-align: center;
}

.widget .extra-temp {
    flex: 1 100%;
    padding: 5% 0;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
    background: #fafafa;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    font-size: 100px;
}

.widget .extra-temp p {
    font-size: 25px;
    font-weight: 600;
}

.temp-info-minmax,
.weather-extra-info,
.two-sided-section {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    text-align: center;
    align-items: center;
    color: #70c1b3;
    /* background-color: blanchedalmond; */
}

.widget .extra-temp .extra-info-leftside {
    text-align: left;
    font-weight: 600;
    text-transform: capitalize;
    color: #080705;
    font-size: 15px;
}

/* input search button style  */
.search {
    right: 5%;
    padding-top: 10px;
    width: 150%;
    position: relative;
    display: flex;
    justify-content: center;
    display: inline-block
}

.searchTerm {
    padding-top: 10px;
    border: 3px solid #00b4cc;
    border: none;
    padding: 10px;
    border-radius: 20px 0px 0px 20px;
    background-color: #d6f2f6;
    color: #333;
    font-size: 16px;
    width: 65%;
    /* box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); */
}

.searchButton {
    position: absolute;
    margin-top: 5px;
    top: 50%;
    border-radius: 0px 20px 20px 0px;
    transform: translateY(-50%);
    border: none;
    background-color: #00b4cc;
    outline: none;
    cursor: pointer;
    font-size: 16px;
    height: 40px;
    color: #333;
    font-size: 20px;
}

.searchTerm:focus {
    background-color: #fafafa;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
    color: #00b4cc;
}

.wrap {
    /* height: 20px; */
    display: flex;
    justify-content: center;
    text-align: center;
    margin-top: -80px;
    margin-bottom: 50px;
}

/*Resize the wrap to see the search bar change!*/
/* .wrap {
    width: 30%;
    position: absolute;
    top: 30%;
    left: 50%;
    transform: translate(-50%, -50%);
} */
@media screen and (max-width: 768px) {
    .widget {
        width: 100%;
        max-width: 400px;
    }
}

@media screen and (max-width: 480px) {
    .widget {
        width: 100%;
        max-width: 300px;
    }
}